body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
    --first-color: rgb(6, 0, 255);
    --second-color: rgb(169, 169, 164);
    --white-alpha-color: rgb(255, 255, 255,0.8);
    
}

.card-container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    padding: 2rem;
}
    .card{
        height: 350px;
        width: 300px;
    }
        .card-image{
            height: 300px;
            position: relative;
            width: inherit;
        }
            .card-front, .card-back{
                height: 300px;
                position: absolute;
            }
            .card-front img{
                height: 300px;   
            }
            .card-back{
                display: flex;
                flex-direction: column;
                justify-content: flex-end;
                opacity: 0;
            }
                .img-back{
                    height: 300px;
                }
                .img-back img{
                    height: inherit;
                }
                .text-back{
                    background-color: rgb(255, 255, 255,0.8);
                    background-color: var(--white-alpha-color);
                    height: 50px;
                    text-align: center;
                    color: rgb(6, 0, 255);
                    color: var(--first-color);
                    text-shadow: 0px 1px rgb(169, 169, 164);
                    text-shadow: 0px 1px var(--second-color);
                    position: absolute;
                    width: 100%;
                    -webkit-transform: scaleY(0);
                            transform: scaleY(0);
                }
                #mouse-over:hover{
                    opacity: 1;
                }
                #mouse-over:hover .text-back{
                    -webkit-transform: scaleY(1);
                            transform: scaleY(1);
                    transition: all 0.1s linear;
                }
        .card-description{
            align-items: center;
            display: flex;
            height: 50px;  
            justify-content: space-between;
        }   
            .title{
                color:rgb(6, 0, 255);
                color:var(--first-color);
                font-weight: 500;
                margin-bottom: 0px;
            }
            .price{
                color: rgb(169, 169, 164);
                color: var(--second-color);
                font-weight:600;
                margin-top: 1px;
            }


.btn{
    background-color: rgb(6, 0, 255);
    border: 0;
    border-radius: 20%;
    padding: 5px;
    width: 40px;
    height: 40px;
   
}

.btn svg{
    fill: #FFFF;
}

.btn:hover{
    background-color: rgb(6, 0, 255, 40%);
}
